@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  margin: 0;
  padding: 0;
}
 
body {
  font-family: 'Poppins', sans-serif;
}



 .container {
  margin: 30px auto;
  min-height: 300px;
  padding: 30px;
  border-radius: 5px;
  max-width: 100%;
} 
.event-table {
    overflow-y: scroll;
    height: 200px;
}
/*
::-webkit-scrollbar {
    width: 0;
    background: transparent;
} */

.inner-div {
    overflow-x: scroll;
    -webkit-overflow-scrolling: touch;
    margin-left: 2rem;
}

.popup {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: absolute;
    padding: 20px;

}

.popup-inner {
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 8px;
    width: 100%;
    background: #ffffff;
}

.popup-inner .close-btn {
    position: absolute;
    top: 16px;
    right: 16px;
}

input[type="file"] {
    display: none;
}

#label {
    color: black;
    border: black 1px solid;
    height: 60px;
    width: 250px;
    background-color: rgb(240, 240, 240);
    margin: auto;
    font-size: 20px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#chart-container {
    width: 100%;
    height: 50%;
    min-height: 420px;
    background: rgb(241, 241, 241);
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

#upload-button {
    width: 50%;
}
#nav-container {
    -webkit-justify-content: none;
            justify-content: none;
}

header {
    width: 100%;
}

#title {
    width: 50%;
}
.container {
    min-width: 95%;
    padding-bottom: 0;
    margin-bottom: 0;
}
