.popup {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    position: absolute;
    padding: 20px;

}

.popup-inner {
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 8px;
    width: 100%;
    background: #ffffff;
}

.popup-inner .close-btn {
    position: absolute;
    top: 16px;
    right: 16px;
}